@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Muli', monospace;
  font-weight: bold;
}

html {
  height: 100%;
  outline: 0;
}
#root {
  height: 100%;
  color: #dfdfdf;
}

body {
  height: 100%;
  background-color: #282828;
}

:focus {
  outline: none;
}
::-moz-focus-inner {
  border: 0;
}

.App {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.toast {
  background-color: #8ec07c !important;
  color: #282828 !important;
  text-align: center !important;
  min-height: 40px !important;
  box-shadow: 0 0px 10px 0 rgba(20, 20, 20, 1) !important;
  margin-bottom: 0rem;
}

.Settings {
  background-color: #282828;
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.SettingsContainer {
  display: grid;
  flex-direction: column;
  grid-gap: 5px;
  justify-items: center;
  padding-top: 5px;
}
.SettingsHeader {
  display: flex;
  justify-content: space-between;
  border-bottom: #8ec07c 1px solid;
  padding-bottom: 5px;
}

.SettingsEntry {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.SettingsEntryTitle {
  font-size: 0.7rem;
  padding-bottom: 5px;
}
.SettingsEntryForm {
  display: grid;
  grid-gap: 2px;
  gap: 2px;
}
.SettingsEntryInput {
  border: none;
  font-size: 0.7rem;
  background-color: #3e4245;
  color: #dfdfdf;
  text-align: center;
  height: 35px;
  width: 300px;
}
.popup-content {
  padding: 0 !important;
  border: none !important;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  max-height: 85%;
  overflow: auto;
}

.TitleBar {
  width: calc(100% - 10px);
  display: flex;
  font-size: 0.8rem;
  justify-content: space-between;
  padding: 5px 5px;
  align-items: center;
  border-bottom: #8ec07c 2px solid;
}
.TitleBarButton {
  background-color: #8ec07c;
  border: none;
  flex-direction: column;
  text-align: center;
  display: flex;
  align-items: center;
  width: 75px;
  height: 48px;
  cursor: pointer;
  justify-content: center;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.TitleBarButton:active {
  border-style: outset;
  border-color: #282828;
}

.CommandButtonContainer {
  height: 100%;
  align-content: center;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;
}
.CommandButton {
  background-color: #8ec07c;
  border: none;
  flex-direction: column;
  text-align: center;
  display: flex;
  align-items: center;
  width: 150px;
  height: 35px;
  cursor: pointer;
  margin: 2px;
  justify-content: center;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.CommandButton:active {
  border-style: outset;
  border-color: #282828;
}

.react-contextmenu-item {
  background-color: #333;
  padding: 5px;
  font-size: 0.8rem;
  border: #8ec07c solid 2px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.react-contextmenu-item--selected {
  background-color: #444;
  padding: 5px;
  font-size: 0.8rem;
  border: #8ec07c solid 2px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

